<template>
    <div>
        <el-container>
            <el-aside width="200px">
                <el-menu  default-active="1">
                    <el-menu-item index="1">
                        <i class="el-icon-user-solid"/>
                        <router-link to="/user/info">個人信息</router-link>
                    </el-menu-item>
                    <el-menu-item index="2">
                        <i class="el-icon-s-order"/>
                        <router-link to="/user/stu">學生管理</router-link>
                    </el-menu-item>
                </el-menu>
            </el-aside>

            <el-container>
                <el-header style="text-align: right; front-size:12px">
                    <el-dropdown>
                        <i class="el-icon-s-tools" style="margin-right: 15px"/>{{userName}}
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item>
                                <router-link to="/user/info">個人信息</router-link>
                            </el-dropdown-item>
                            <el-dropdown-item>退出登錄</el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>
                </el-header>

                <el-main>
                    <router-view/>
                </el-main>
            </el-container>
        </el-container>
    </div>
</template>
<script>
export default({
    name: 'User',
    data(){
        return {
            userName: this.$route.query.userName
        }
    },
})
</script>

<style scoped lang="css">
.el-header{
    background-color: #B3C0D1;
    color: #333;
    line-height: 60px;
}
.el-aside{
    color: #333;
}
</style>


